	export default {

		data() {
			return {
				titleNViewBackground: '',
				swiperCurrent: 0,
				swiperLength: 0,
				adList: [],
				navList: [],
				goodsList: [],
				listQuery: { //查询分页
					name:"", 
					pageNo: 1,
					pageSize: 10
				},
			};
		},

		onLoad() {
			this.loadData();
		},
		methods: {
			imgPath:function(path){
				if(path==''||path==null||path=='null'){
					return "/static/errorImage.jpg"				 
				}else if (path!=''&&path.indexOf('http')>=0) {
					return path;				 
				}
 				return this.siteHost + path;
			},
			/**
			 * 请求静态数据只是为了代码不那么乱
			 * 分次请求未作整合
			 */
			async loadData() {
				var _this = this;
				//获取首页内容数据
				this.sendRequest({
					url : "get_home",
					data : {},
					method : "get",
 					success:function (res) {
						_this.navList=res.data.navList;
 						let adList= res.data.adList;
						_this.swiperLength = adList.length;
						_this.adList = adList; 
 					}
				});   
 				this.sendRequest({
					url : "get_all_product",
					data : _this.listQuery,
					method : "get",
					header: {'content-type' : "application/x-www-form-urlencoded"},
					success:function (res) {
						let goodsPage= res.data;  
						let goodsList = goodsPage.datas;
						if(_this.listQuery.pageNo==1){
							_this.goodsList = [];
						}
						_this.goodsList = _this.goodsList.concat(goodsList);  
					}
				});
			},
			//轮播图切换修改背景色
			swiperChange(e) {
				const index = e.detail.current;
				this.swiperCurrent = index;
				// this.titleNViewBackground = this.adList[index].background;
			},
			//详情页
			navToDetailPage(item) {
				//测试数据没有写id，用title代替
				let id = item.id; 
				uni.navigateTo({
					url: `/pages/product/product?id=${id}`
				})
			},
			//导航跳转
			navDetail(item){
				if (item.url!=''&&item.url.indexOf('http')>=0) {
					window.location.href =item.url;
				}else{
					uni.navigateTo({
						url: item.url
					})
				} 
			}
		},

























	}
